body {
  font-family: Helvetica, Tahoma, Arial, "Microsoft YaHei", "Hiragino Sans GB", "WenQuanYi Micro Hei", sans-serif;
}
body,div,h1,h2,h3,h4,h5,h6,section {
  margin: 0;
  padding: 0;
}
.show {
  display: block;
}
.hide {
  display: none;
}
.root {
  display: flex;
  min-height: 100vh;
  margin: 0;
}
.popup {
  margin: auto;
}

.btn {
  box-shadow: inset 0 1px 0 rgba(255,255,255,0.2);
  background-color: #ffffff;
  border-color: #198764;
  text-align: center;
  height: 37px;
  line-height: 37px;
  border-radius: 5px;
  cursor: pointer;
  border: 1px solid #1d9d74;
}
.btn:hover {
  color: #fff;
  background-color: #1d9d74;
}
a {
  text-decoration: none;
}